const controls = GUI(state, {
    className: 'controls',
    containerCSS: `
        position: fixed;
        top: 0;
        right: 5px;
        max-width: 300px;
        max-height: 100vh;
        overflow: auto;
    `
});

const listeners = {
    active: [],
    add(el, type, f) {
        const binding = [el, type, f];

        this.active.push(binding);
        el.addEventListener(type, f);

        return binding;
    },
    remove(binding) {
        const [el, type, f] = binding;

        el.removeEventListener(type, f);
        this.active.splice(this.active.indexOf(binding), 1);
    },
    removeAll() {
        this.active.forEach(([el, type, f]) => el.removeEventListener(type, f));
        this.active.length = 0;
    }
};

let socket;
let version = 0;

if(module.hot) {
    module.hot.dispose(() => {
        console.log('hot reload - dispose');
        listeners.removeAll();
        (regl && regl.destroy());
        (controls && document.querySelector('.controls').remove());
        (socket && socket.disconnect());
    });

    module.hot.accept(() => console.log('hot reload - accept'));
}

const URL = (window.URL || window.webkitURL);

const fileInput = document.querySelector('.file-input');

const input = { canvas: document.createElement('canvas') };

input.context = input.canvas.getContext('2d');

const output = { canvas: document.querySelector('canvas') };

const regl = output.regl = getRegl({
    extensions: ['oes_texture_float', 'oes_texture_float_linear'],
    canvas: output.canvas,
    attributes: { antialias: false, depth: false },
    pixelRatio: Math.max(+devicePixelRatio, 1.5)
});

let distances = regl.texture();
const framebuffer = regl.framebuffer();

const draw = regl({
    vert: screenVert,
    frag: drawFrag,
    count: 3,
    attributes: { position: screenPositions },
    uniforms: {
        viewWidth: regl.context('viewportWidth'),
        viewHeight: regl.context('viewportHeight'),
        tick: regl.context('tick'),
        time: regl.context('time'),

        colorIn0: (c, { state: { draw: { color: { inside: { from: hex } } } } }) =>
            colorConvert.hex.rgb.raw(hex).map((c) => c/255),

        colorIn1: (c, { state: { draw: { color: { inside: { to: hex } } } } }) =>
            colorConvert.hex.rgb.raw(hex).map((c) => c/255),

        colorInMix: regl.prop('state.draw.color.inside.mix'),

        colorOut0: (c, { state: { draw: { color: { outside: { from: hex } } } } }) =>
            colorConvert.hex.rgb.raw(hex).map((c) => c/255),

        colorOut1: (c, { state: { draw: { color: { outside: { to: hex } } } } }) =>
            colorConvert.hex.rgb.raw(hex).map((c) => c/255),

        colorOutMix: regl.prop('state.draw.color.outside.mix'),

        speed: regl.prop('state.draw.speed'),
        slice: regl.prop('state.draw.slice'),
        scale: regl.prop('state.draw.scale'),
        thick: regl.prop('state.draw.thick'),
        smoothing: regl.prop('state.draw.smoothing'),
        curve: regl.prop('state.draw.curve'),
        warpAmount: regl.prop('state.draw.warp.amount'),
        warpCurve: regl.prop('state.draw.warp.curve'),
        warpSpeed: regl.prop('state.draw.warp.speed'),
        warpScale: regl.prop('state.draw.warp.scale'),
        distanceSmooth: regl.prop('state.draw.distanceSmooth.amount'),
        distanceOffset: regl.prop('state.draw.distanceSmooth.offset'),
        showDistance: (c, { state: { draw: { showDistance } } }) => +showDistance,

        cutoff: regl.prop('layers.cutoff'),
        distances: regl.prop('distances')
    },
    framebuffer
});

const post = regl({
    vert: screenVert,
    frag: postFrag,
    count: 3,
    attributes: { position: screenPositions },
    uniforms: {
        framebuffer,
        width: ({ framebufferWidth: w }) => w,
        height: ({ framebufferHeight: h }) => h
    }
});

let image;

function updateSize(width, height) {
    input.canvas.width = output.canvas.width = width;
    input.canvas.height = output.canvas.height = height;
    framebuffer.resize(width, height);
}

function setupSDF(skipImage = !state.image.url) {
    if(!skipImage) {
        if(image && Math.min(image.width, image.height)) {
            updateSize(image.width, image.height);
            input.context.drawImage(image, 0, 0);
        }
        else {
            console.log('no image to draw');
            return updateSize(0, 0);
        }
    }

    distances({
        width: input.canvas.width,
        height: input.canvas.height,
        data: input.context,
        format: 'luminance',
        type: 'float',
        mag: 'linear',
        min: 'linear'
    });
}

function clearImage() {
    (image && URL.revokeObjectURL(image.src));

    return image = null;
}

function setupImage() {
    clearImage();

    return image = new Image();
}

function onFile(file) {
    if(file) {
        state.image.url = URL.createObjectURL(file);
    }
    else {
        clearImage();
        setupSDF();
    }
}

function fullscreen() {
    output.canvas.requestFullscreen();
}

const getLayers = () => layers.keys.filter((key) => state.image.layers[key]);

function updateLayers(keys, latest) {
    console.log('New route', keys, latest, version);

    if(latest > version) {
        version = latest;

        const updates = layers.keys.reduce((out, k) => {
                const on = (keys.indexOf(k) >= 0);

                ((state.image.layers[k] !== on) && ((out || (out = {}))[k] = on));

                return out;
            },
            false);

        (updates && Object.assign(state.image.layers, updates));
        mergeLayers(keys);
        (socket && socket.emit('route', keys, version));
    }
    else if(latest < version) {
        (socket && socket.emit('route', getLayers(), version));
    }
}

const mergeLayers = (keys = getLayers()) =>
    ((keys.length)?
        Promise.all(keys.map((key) => new Promise((y) => {
                const layerImage = new Image();

                listeners.add(layerImage, 'load', () => y(layerImage));
                layerImage.src = `${rootPath}assets/holds/sdf/${key}.png`;
            })))
            .then((images) => {
                // Assumes images are all the same dimensions
                const [{ width, height }] = images;

                updateSize(width, height);

                // Use the closest (highest) values in SDF layers.
                input.context.globalCompositeOperation = 'lighten';
                images.forEach((image) => input.context.drawImage(image, 0, 0));
                setupSDF(true);
            })
            .catch(console.error)
    :   updateSize(0, 0));

function setupSocket(url) {
    (socket && socket.disconnect());

    socket = ((url)?
            io.connect(url)
                .on('connection', (connected) => console.log('connection', connected))
                .on('disconnection', (connected) => console.log('disconnection', connected))
                .on('route', updateLayers)
        :   null);
}

state.$onChanges((changes) => {
    const changed = Object.keys(changes).join('\n');

    ((changed.search(/^image\.layers\.[0-9]+$/gi) >= 0) &&
        updateLayers(getLayers(), version+1));

    if('display.scale' in changes) {
        const { scale } = state.display;

        output.canvas.style.transform = ((scale !== 1)? `scale(${scale})` : '');
    }

    if('image.url' in changes) {
        if(changes['image.url']) {
            setupImage().src = state.image.url;
            listeners.add(image, 'load', () => setupSDF());
        }
        else {
            clearImage();
            setupSDF();
        }
    }

    (('socket' in changes) && setupSocket(state.socket));
});

state.image.url = state.image.url;

listeners.add(fileInput, 'change', () => onFile(fileInput.files[0]));
listeners.add(document.body, 'dragover', (e) => e.preventDefault());

listeners.add(document.body, 'drop', (e) => {
    e.preventDefault();

    let file;

    if(e.dataTransfer.items) {
        // Use DataTransferItemList interface to access the file(s)
        for(let d = 0; d < e.dataTransfer.items.length; ++d) {
            // If dropped items aren't files, reject them
            if(e.dataTransfer.items[d].kind === 'file') {
                file = e.dataTransfer.items[d].getAsFile();
                break;
            }
        }
    }
    else {
        // Use DataTransfer interface to access the file(s)
        file = e.dataTransfer.files[0];
    }

    onFile(file);
});

const props = { state, layers, distances };

regl.frame(() => {
    regl.clear({
        color: [0, 0, 0, 255],
        depth: 1,
        framebuffer
    });

    draw(props);
    post();
});

setupSocket(state.socket);
